import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { Main } from './Main'

export const Search = () => {
    const [data, setData] = useState(null)
    const [input, setInput] = useState('')

    const handleSubmit = async (e) => {
      e.preventDefault()
      
      const res = await fetch(`https://api.mercadolibre.com/sites/MLM/search?nickname=${input}`)
      const data = await res.json()
      setData(data)

      
      
    
    }

    console.log(data);

    return (
        <>
          <div class="container-xl">
         <form class="form-inline my-2 my-lg-0 mt-2"
         onSubmit={handleSubmit}
         >
            <input class="form-control mr-sm-3 w-50" type="search" placeholder="Buscar vendedor por Nickname" aria-label="Search"
                //onChange={setInput()}
                onChange={(e) => setInput(e.target.value)}
            />
            <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Buscar</button>
         </form>
         </div>

         <Main data={data}/>
         </>
        
    )
}
