import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { Producto } from './producto'

export const Resultados = (props) => {
    const [productos, setProductos] = useState([])
    const [cargando, setCargando] = useState(false)
    const { data } = props
    
    useEffect(() => {
        data.results.map(producto => {
            return  productos.push(
           
                {
                    id: producto.id,
                    titulo: producto.title,
                    precio: producto.price,
                    ventas: producto.sold_quantity,
                    disponibles: producto.available_quantity,
    
                })
            
        })
    
        
        productos.map(async(producto) => {
            setCargando(true)
            const res = await fetch(`https://api.mercadolibre.com/visits/items?ids=${producto.id}`)
            const data = await res.json()
            producto.vistas = data[producto.id]
          
            const res1 = await fetch(`https://api.mercadolibre.com/items?ids=${producto.id}`) 
            const data1 = await res1.json()
            producto.imagen =  data1[0].body.pictures[0].url
            let fecha =  data1[0].body.date_created
            producto.publicado = fecha.split('T')[0]

            const res2 = await fetch(`https://api.mercadolibre.com/questions/search?item=${producto.id}`) 
            const data2 = await res2.json()
            producto.preguntas = data2.total

            const res3 = await fetch(`https://api.mercadolibre.com/reviews/item/${producto.id}`) 
            const data3 = await res3.json()
          
            producto.opiniones = {
                
                total: data3.paging.total,
                escala: data3.rating_average,
                estrellas: data3.rating_levels
            }
          
            
            setCargando(false)
        })
       
       /*  productos.map( async(producto) => {
            setCargando(true)
            const res = await fetch(`https://api.mercadolibre.com/items?ids=${producto.id}`) 
            const data = await res.json()
            producto.imagen =  data[0].body.pictures[0].url
            producto.publicado =  data[0].body.date_created

            setCargando(false)
        }) 
        
        productos.map( async(producto) => {
            setCargando(true)
            const res = await fetch(`https://api.mercadolibre.com/questions/search?item=${producto.id}`) 
            const data = await res.json()
          
            producto.preguntas = data.total
            setCargando(false)
        }) 
    
    
         productos.map( async(producto) => {
            setCargando(true)
            const res = await fetch(`https://api.mercadolibre.com/reviews/item/${producto.id}`) 
            const data = await res.json()
          
            producto.opiniones = {
                
                total: data.paging.total,
                escala: data.rating_average,
                estrellas: data.rating_levels
            }
            setCargando(false)
                
        })  */

    }, [])

    
   
    
 
    console.log(productos);
    
   

        return (

            

            
            <div className="container-xl">
                {
                    !cargando ?
                    <div>
                    <div className="alert alert-success mt-2" role="alert">
                    Aqui te tengo todos los resultados de {data.seller.nickname}
                </div>
                
                <div className="card">
                    <div className="card-body">
                        <h1>Informacion del vendedor</h1>
                    </div>
                    <div className="card-body">
    
                        <div className="row">
    
    
                            <div className="col">
                                <div className="card-body">
                                    Total ventas: {data.seller.seller_reputation.transactions.total}
                                </div>
                            </div>
    
                            <div className="col">
                                <div className="card-body">
                                    Total Concretadas: {data.seller.seller_reputation.transactions.completed}
                                </div>
                            </div>
    
                            <div className="col">
                                <div className="card-body">
                                    Total Canceladas: {data.seller.seller_reputation.transactions.canceled}
                                </div>
                            </div>
    
                        </div>
                        <div className="row">
                            <div className="col">
                                <div className="card-body">
                                    Empezo a vender en Mercado libre: {data.seller.registration_date.split('T')[0]}
                                </div>
                            </div>
    
                            <div className="col">
                                <div className="card-body">
                                    Ultimos 60 dias: {data.seller.seller_reputation.metrics.sales.completed}
                                </div>
                            </div>
    
                            <div className="col">
                                <div className="card-body">
                                    Reputacion
                                     {data.seller.seller_reputation.level_id === '5_green' ? <h4>Verde Oscuro</h4> : <h4> Abajo de verde oscuro</h4>}
                                    {data.seller.seller_reputation.power_seller_status}
                                </div>
                            </div>
    
                        </div>
    
                    </div>
                </div>
    
                <div className="card">
    
                    <div className="card-body">
                        <h1>Informacion de los productos</h1>
                        <h2>Total de los Productos {data.results.length} </h2>
                    </div>
    
                    <div className="card-body">
                    <div className="container-xl">
                  
                    <Producto productos={productos}/>
                    </div>
                    </div>
    
                </div>
                </div>
                :
                <h1>Cargando...</h1>
                }
                
    
            </div>
    
    
    
        )
    }
    
    

