import React from 'react'
import { useState } from 'react';

export const Producto = (props) => {
    const [productos, setProductos] = useState(props.productos)
    console.log(productos);
    return (

    
        <div class="row">
        {
            props.productos.map(producto => {
                console.log(producto);
                console.log('estas son las vistas', producto.visitas);
                console.log(producto.imagen);
                console.log(producto.preguntas);
                return (
                    <div key={producto.id}  class="col mt-5">
                    <div class="card" style={{ "width": "18rem" }}>
                        <img src={producto.imagen} class="card-img-top" alt="..." />
                        <div class="card-body">
                          <p class="card-text">{producto.titulo}</p>
                          <p class="btn btn-info mr-5"> $ {producto.precio}</p>
                          <p class="btn btn-warning">Total de vistas {producto.vistas}</p>
                          <p class="btn btn-success">Total de ventas: {producto.ventas}</p>
                          <p class="btn btn-info">Total disponible: {producto.disponibles}</p>
                          <p class="btn btn-danger">Total preguntas: {producto.preguntas}</p>
                          <p class="btn btn-info">Se publico el: {producto.publicado}</p>
                          
                        </div>
                    </div>
                    </div>
                )
            })

        }
    </div> 
    )
}
