import React from 'react'
import { Resultados } from './resultados'

export const Main = (props) => {

    const {data} = props
    if(data){
    if (data.results.length > 0) {
        return (
           <Resultados data={data}/>
        )
    }
    
    else {
        return (
      <div class="container-xl">
      <h1>El vendedor no existe en mercado libre </h1>
      <h2>Ponte abusado primo y busca un vendedor que exista</h2>
      </div>
        )
    }
  }else {
    return (

      <div class="container-xl">

        <h1>Pon el nombre de un vendedor si quieres que te ense√±e sus secretos cabron!</h1>
      </div>
    )
  }
   
}
